.profile-flex {
  display: flex;
  align-items: center;
  gap: 2rem; // spacing between photo and text
  flex-wrap: wrap; // responsive for smaller screens
}

.myPhotoContainer img {
  width: 350px;
  height: auto;
  border-radius: 10px;
  object-fit: cover;
}

.introText {
    font-size: 1rem;
}
.profile-text {
  flex: 1;
}

